"use client";

import { useEffect, useState, useMemo } from "react";
import { useTeams } from "@/features/teams/lib/use-teams";
import { Team } from "@/entities/team/model/types";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/shared/ui/card";
import { Button } from "@/shared/ui/button";
import { Input } from "@/shared/ui/input";
import { Label } from "@/shared/ui/label";
import { useToast } from "@/shared/ui/use-toast";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/shared/ui/dialog";
import { User, Users, Plus, Copy } from "lucide-react";
import { useAuthContext } from "@/app/providers/auth-provider";

export default function TeamsPage() {
  const { isLoading, teams, createTeam, joinTeam, fetchMyTeams } = useTeams();
  const { toast } = useToast();
  const { user } = useAuthContext();
  const [isCreateDialogOpen, setIsCreateDialogOpen] = useState(false);
  const [isJoinDialogOpen, setIsJoinDialogOpen] = useState(false);
  const [newTeamName, setNewTeamName] = useState("");
  const [joinCode, setJoinCode] = useState("");

  // Check if user already has a created team
  const hasCreatedTeam = useMemo(() => {
    if (!user?.id || !teams) return false;
    return teams.some((team) => team.creatorId === Number(user.id));
  }, [teams, user?.id]);

  useEffect(() => {
    fetchMyTeams().catch(console.error);
  }, [fetchMyTeams]);

  const handleCreateTeam = async (e: React.FormEvent) => {
    e.preventDefault();

    if (hasCreatedTeam) {
      toast({
        title: "Ошибка",
        description:
          "Вы уже создали команду. Можно создать только одну команду.",
        variant: "destructive",
      });
      return;
    }

    try {
      await createTeam({ name: newTeamName });
      setIsCreateDialogOpen(false);
      setNewTeamName("");
      toast({
        title: "Успешно",
        description: "Команда успешно создана",
      });
    } catch (error) {
      console.error("Error creating team:", error);
      toast({
        title: "Ошибка",
        description:
          error instanceof Error ? error.message : "Не удалось создать команду",
        variant: "destructive",
      });
    }
  };

  const handleJoinTeam = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      await joinTeam(Number(joinCode));
      setIsJoinDialogOpen(false);
      setJoinCode("");
      toast({
        title: "Успешно",
        description: "Вы успешно присоединились к команде",
      });
    } catch (error) {
      console.error("Error joining team:", error);
      toast({
        title: "Ошибка",
        description:
          error instanceof Error
            ? error.message
            : "Не удалось присоединиться к команде",
        variant: "destructive",
      });
    }
  };

  const copyTeamCode = (code: string) => {
    navigator.clipboard.writeText(code);
    toast({
      title: "Код скопирован",
      description: "Код команды скопирован в буфер обмена",
    });
  };

  if (isLoading) {
    return (
      <div className="flex items-center justify-center min-h-[60vh]">
        <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-primary"></div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl md:text-3xl font-bold tracking-tight">
            Команды
          </h1>
          <p className="text-sm md:text-base text-gray-600">
            Управление командами и участниками
          </p>
        </div>
        <div className="flex gap-2">
          <Dialog open={isJoinDialogOpen} onOpenChange={setIsJoinDialogOpen}>
            <DialogTrigger asChild>
              <Button variant="outline">Присоединиться</Button>
            </DialogTrigger>
            <DialogContent>
              <DialogHeader>
                <DialogTitle>Присоединиться к команде</DialogTitle>
                <DialogDescription>
                  Введите код команды, чтобы присоединиться к ней
                </DialogDescription>
              </DialogHeader>
              <form onSubmit={handleJoinTeam}>
                <div className="space-y-4 py-4">
                  <div className="space-y-2">
                    <Label htmlFor="code">Код команды</Label>
                    <Input
                      id="code"
                      value={joinCode}
                      onChange={(e) => setJoinCode(e.target.value)}
                      placeholder="Введите код команды"
                    />
                  </div>
                </div>
                <DialogFooter>
                  <Button type="submit">Присоединиться</Button>
                </DialogFooter>
              </form>
            </DialogContent>
          </Dialog>

          <Dialog
            open={isCreateDialogOpen}
            onOpenChange={setIsCreateDialogOpen}
          >
            <DialogTrigger asChild>
              <Button
                disabled={hasCreatedTeam}
                title={hasCreatedTeam ? "Вы уже создали команду" : ""}
              >
                <Plus className="h-4 w-4 mr-2" />
                Создать команду
              </Button>
            </DialogTrigger>
            <DialogContent>
              <DialogHeader>
                <DialogTitle>Создать новую команду</DialogTitle>
                <DialogDescription>
                  {hasCreatedTeam
                    ? "Вы уже создали команду. Можно создать только одну команду."
                    : "Создайте команду для совместной работы над проектом"}
                </DialogDescription>
              </DialogHeader>
              <form onSubmit={handleCreateTeam}>
                <div className="space-y-4 py-4">
                  <div className="space-y-2">
                    <Label htmlFor="name">Название команды</Label>
                    <Input
                      id="name"
                      value={newTeamName}
                      onChange={(e) => setNewTeamName(e.target.value)}
                      placeholder="Введите название команды"
                      disabled={hasCreatedTeam}
                    />
                  </div>
                </div>
                <DialogFooter>
                  <Button type="submit" disabled={hasCreatedTeam}>
                    Создать команду
                  </Button>
                </DialogFooter>
              </form>
            </DialogContent>
          </Dialog>
        </div>
      </div>

      {teams.length === 0 ? (
        <Card>
          <CardContent className="flex flex-col items-center justify-center py-10">
            <Users className="h-12 w-12 text-gray-400 mb-4" />
            <h3 className="text-lg font-medium text-gray-900 mb-2">
              У вас пока нет команд
            </h3>
            <p className="text-sm text-gray-600 text-center max-w-sm mb-6">
              Создайте новую команду или присоединитесь к существующей,
              используя код приглашения
            </p>
            <div className="flex gap-4">
              <Button
                variant="outline"
                onClick={() => setIsJoinDialogOpen(true)}
              >
                Присоединиться к команде
              </Button>
              <Button onClick={() => setIsCreateDialogOpen(true)}>
                Создать команду
              </Button>
            </div>
          </CardContent>
        </Card>
      ) : (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {teams.map((team) => (
            <Card key={team.id}>
              <CardHeader>
                <CardTitle>{team.name}</CardTitle>
                <CardDescription>
                  Код команды:{" "}
                  <Button
                    variant="link"
                    className="px-0"
                    onClick={() => copyTeamCode(team.code)}
                  >
                    {team.code}
                    <Copy className="h-4 w-4 ml-1" />
                  </Button>
                </CardDescription>
              </CardHeader>
              <CardContent>
                <div className="flex items-center gap-2">
                  <User className="h-4 w-4" />
                  <span className="text-sm text-gray-600">
                    {team.creatorId === Number(user?.id)
                      ? "Создатель"
                      : "Участник"}
                  </span>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>
      )}
    </div>
  );
}
